import React from 'react';
import cv from '../../media/assets/cv.pdf'

const Cta = () => {
  return (
    <div className="cta">
      <a href={cv} download className="btnn">
        Download CV
      </a>
      <a href="#contact" className="btnn btnn-primary">
        Let's Talk
      </a>
    </div>
  );
};

export default Cta;
